<template>
    <v-card tile flat>
        <v-toolbar 
            short 
            color="primary" 
            dark 
            class="elevation-0"
        >
            <v-btn icon @click="go_to_home" class="mr-3">
                <v-img width="1" :src="logo" />
            </v-btn>

            <v-spacer></v-spacer>

            <v-btn icon>
              <v-icon color="secondary">fa fa-bell</v-icon>
            </v-btn>

            <v-btn @click="signout" icon>
                <v-icon color="secondary">fas fa-sign-out-alt</v-icon>
            </v-btn>
        </v-toolbar>
    </v-card>
</template>

<script>
export default {
    data: () => ({
        logo: require('../assets/logo.png')
    }),
    methods: {
        go_to_home(){
            if (this.$router.currentRoute.name != "Home") {
                this.$router.push({ name: "Home" })
            }
        },
        signout(){
            this.$router.push({ path: "/signout" })
        }
    }
}
</script>