<template>
    <v-card flat tile>
        <line-chart v-if="show_graph" ref="chart" :chart-data="graph_data" :options="options" />

        <v-card-actions>
            <v-spacer></v-spacer>
            <v-btn text @click="reset_zoom">Reset</v-btn>
        </v-card-actions>
    </v-card>
</template>

<script>
import LineChart from './LineChart.js'
import zoom from 'chartjs-plugin-zoom';
import {mapActions, mapState} from 'vuex'

export default {
    data: () => ({
        show_graph: true,
        data: {
            datasets: []
        },
        graph_data: {},
        options: {
            responsive: true,
            maintainAspectRatio: false,
            scales: {
                xAxes: [{
                    type: 'time',
                    //distribution: 'series',
                    distribution: 'linear',
                    time: {
                        unit: 'hour'
                    }
                }]
            },
            plugins: {
                zoom: {
                    zoom: {
                        enabled: true,
                        drag: true,
                        mode: 'xy'
                    }
                }
            }
        }
    }),
    props: {
        type: String,
        reservoir: Number
    },
    computed: {
        ...mapState('data', ['time_measurements', 'value_measurements'])
    },
    components: {
        LineChart
    },
    watch: {
        time_measurements(){
            var weekday = new Array(7);
            weekday[0] = "Segunda";
            weekday[1] = "Terça";
            weekday[2] = "Quarta";
            weekday[3] = "Quinta";
            weekday[4] = "Sexta";
            weekday[5] = "Sábado";
            weekday[6] = "Domingo";

            this.data.labels = this.time_measurements.map( function (time){
                let hour = time.split(' ')[1]
                time = time.split(' ')[0]
                let [day, month, year] = time.split('/')

                return year+'-'+month+'-'+day+'T'+hour
            })

            this.graph_data = JSON.parse(JSON.stringify(this.data))
        },
        value_measurements(){
            this.data.datasets[0].data = this.value_measurements

            this.graph_data = JSON.parse(JSON.stringify(this.data))
        },
        reservoir(){
            this.get_all_measurements({'reservoir_id': this.reservoir, 'type': this.type})
        },
        type(){
            this.update_data()
        }
    },
    methods: {
        ...mapActions('data', ['get_all_measurements']),
        reset_zoom(){
            this.show_graph = false
            
            setTimeout(() => this.show_graph = true, 1);
        },
        update_data(){
            this.data.datasets[0] = {backgroundColor: '#390360'}
            this.data.datasets[0].fill = 1

            if(this.type == 'wqi')          this.data.datasets[0].label = 'IQA'
            else if(this.type == 'temp')    this.data.datasets[0].label = 'Temperatura'
            else if(this.type == 'ph')      this.data.datasets[0].label = 'pH'
            else if(this.type == 'do')      this.data.datasets[0].label = 'Oxigênio Dissolvido'
            else if(this.type == 'ec')      this.data.datasets[0].label = 'Condutividade Elétrica'
            else if(this.type == 'tds')     this.data.datasets[0].label = 'Totais de Sólidos Dissolvidos'
            else if(this.type == 'sal')     this.data.datasets[0].label = 'Salinidade'
            else if(this.type == 'turb')    this.data.datasets[0].label = 'Turbidez'
            else if(this.type == 'vol')     this.data.datasets[0].label = 'Volume'
            else if(this.type == 'cons')    this.data.datasets[0].label = 'Consumo'

            this.get_all_measurements({'reservoir_id': this.reservoir, 'type': this.type})
        }
    },
    mounted(){
        this.update_data()
    }
}
</script>